import { defineComponent, computed, ref, watch } from 'vue';
import { useStore } from '@/store';
import { usePermission } from '@/hooks/use-permissions';
import { ElMessageBox } from 'element-plus';
import 'element-plus/es/components/message-box/style/css'

import HyTable from '@/base-ui/table';
export default defineComponent({
  props: {
    contentTableConfig: {
      type: Object,
      required: true
    },
    pageName: {
      type: String,
      required: true
    }
  },
  components: {
    HyTable
  },
  emits: ['newBtnClick', 'editBtnClick'],

  setup(props, {
    emit
  }) {
    const store = useStore(); //0.获取操作的权限

    const isCreate = usePermission(props.pageName, 'create');
    const isUpdate = usePermission(props.pageName, 'update');
    const isDelete = usePermission(props.pageName, 'delete');
    const isQuery = usePermission(props.pageName, 'query'); //1.双向绑定pageInfo

    const pageInfo = ref({
      currentPage: 1,
      pageSize: 10
    });
    watch(pageInfo, () => getPageData()); //2.发送网络请求

    const getPageData = (queryInfo = {}) => {
      if (!isQuery) return;
      store.dispatch('system/getPageListAction', {
        pageName: props.pageName,
        queryInfo: {
          offset: (pageInfo.value.currentPage - 1) * pageInfo.value.pageSize,
          size: pageInfo.value.pageSize,
          ...queryInfo
        }
      });
    };

    getPageData(); //3.从vuex中获取数据

    const dataList = computed(() => store.getters[`system/pageListData`](props.pageName));
    const dataCount = computed(() => store.getters[`system/pageListCount`](props.pageName)); //4.获取其他的动态插槽名称

    const otherPropSlots = props.contentTableConfig?.propList.filter(item => {
      if (item.slotName === 'status') return false;
      if (item.slotName === 'createAt') return false;
      if (item.slotName === 'updateAt') return false;
      if (item.slotName === 'handler') return false;
      return true;
    }); //5.删除/编辑/新建操作

    const handleDeleteClick = item => {
      console.log(item);
      store.dispatch('system/deletePageDataAction', {
        pageName: props.pageName,
        id: item.id
      });

      if (item.id < 10) {
        ElMessageBox.alert('<strong style="color:red">user id小于10不允许删除</strong>', '警告', {
          confirmButtonText: 'OK',
          dangerouslyUseHTMLString: true,
          center: true
        });
      }
    };

    const handleNewClick = () => {
      emit('newBtnClick');
    };

    const handleEditClick = item => {
      emit('editBtnClick', item);
    };

    return {
      dataList,
      getPageData,
      dataCount,
      pageInfo,
      otherPropSlots,
      isCreate,
      isUpdate,
      isDelete,
      handleDeleteClick,
      handleNewClick,
      handleEditClick
    };
  }

});